import React from 'react';
import './App.css';

const goodsFromServer = [
  'Dumplings',
  'Carrot',
  'Eggs',
  'Ice cream',
  'Apple',
  'Bread',
  'Fish',
  'Honey',
  'Jam',
  'Garlic',
];

class App extends React.Component {
  state = {
    clicked: false,
  }

  StartClick = () => {
    this.setState({ clicked: true });
  }

  render() {
    const startButtonClicked = this.state.clicked;

    return (
      <div className="App">
        {startButtonClicked
          ? <GoodsList goods={this.state.goodsCopy} />
          : (
            <button
              type="button"
              onClick={this.StartClick}
            >
              Start
            </button>
          )
        }
      </div>
    );
  }
}

class GoodsList extends React.Component {
  state = {
    goodsCopy: [...goodsFromServer],
  }

  ReverseGoods = () => {
    this.setState(({ goodsCopy }) => ({
      goodsCopy: [...goodsCopy].reverse(),
    }));
  }

  SortGoods = () => {
    this.setState(({ goodsCopy }) => ({
      goodsCopy: [...goodsCopy].sort(),
    }));
  }

  ResetGoods = () => {
    this.setState(() => ({
      goodsCopy: [...goodsFromServer],
    }));
  }

  SortByLengthGoods = () => {
    this.setState(({ goodsCopy }) => ({
      goodsCopy: [...goodsCopy].sort((a, b) => a.length - b.length),
    }));
  }

  render() {
    const listOfGoods = this.state.goodsCopy;

    return (
      <>
        <h1>Goods</h1>
        <button
          type="button"
          onClick={this.ReverseGoods}
        >
          Reverse
        </button>
        <button
          type="button"
          onClick={this.SortGoods}
        >
          Sort alphabetically
        </button>
        <button
          type="button"
          onClick={this.ResetGoods}
        >
          Reset
        </button>
        <button
          type="button"
          onClick={this.SortByLengthGoods}
        >
          Sort by length
        </button>
        <ul className="list">
          {listOfGoods.map(good => (
            <li className="list__item" key={good}>{good}</li>
          ))}
        </ul>
      </>
    );
  }
}

export default App;
